
.card {
    background: rgb(255, 255, 255);
    border-radius: 2px;
    display: inline-block;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 330px;
  }
  
  .card-2 {
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  }
  .flex-container {
    display: flex;
    flex-direction: column;

  }
  
  .flex-container > div {
    width:'100%';
    height: auto;

  }

  .flex-container > div>div {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 10px;

  }

  .flex-container {
    display: flex;
    height: 250px;
    flex-wrap: wrap;
    align-content: space-between;
  }
  
  .flex-container > div {
    background-color: #ffffff;
    width:165px;
    padding-top: 50px;
    text-align: center;
    height: 125px;
    font-size: 30px;
  }