ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background: #f6f5f7;
  }
  
  li {
    float: left;
  }
  
  .Link {
    display: block;
    color: #111;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover:not(.active) {
    background-color: #111;
  }
  
  .active {
    background-color: #11132C;
    border-bottom: 2px solid #4356D5;
  }

  