
  
  .container {
	position: absolute;
	transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
  }
  
  .form {
	padding-top:30px;
	background: #efeeee;
	height: "100%";
	width: 300px;
	border-radius: 20px;
	box-shadow:
	  10px 10px 16px -2px #ccc,
	  -10px -10px 16px -2px #fff;
	text-align: center;
	caret-color: transparent;
  }
  
  p .form {
	color: #777;
	margin-bottom: 1.5em;
	font-size: 24px;
	font-weight: 300;
  }
  
  .form [type="text"]  {
	width: 290px;
	border: none;
	border-radius: 5000px;
	border-color: #fff;
	padding: 1em;
	background: #efeeee;
	box-shadow:
	  inset 4px 4px 6px #ccc,
	  inset -4px -4px 6px #fff;
	margin-bottom: 1.5em;
    caret-color: #000000;
	font-family: 'Poppins', sans-serif;
  }
  .form [type="email"]  {
	width: 290px;
	border: none;
	border-radius: 5000px;
	padding: 1em;
	background: #efeeee;
	box-shadow:
	  inset 4px 4px 6px #ccc,
	  inset -4px -4px 6px #fff;
	margin-bottom: 2em;
	color: #888;
	font-family: 'Poppins', sans-serif;
  }
  .form [type="password"]  {
	width: 290px;
	border: none;
	border-radius: 5000px;
	padding: 1em;
	background: #efeeee;
	box-shadow:
	  inset 4px 4px 6px #ccc,
	  inset -4px -4px 6px #fff;
	margin-bottom: 2em;
	color: #888;
	font-family: 'Poppins', sans-serif;
  }
  .form [type="submit"] {
	  width: 250px;
	  height: 40px;
	  border-radius: 20px;
	  border-color: #fff;
	  box-shadow:
		4px 4px 6px #ccc,
		-4px -4px 6px #fff;
	  margin-top: 1em;
	  font-family: 'Poppins', sans-serif;
	  font-weight: 400;
	}
	.form 	[type="button"] {
		width: 250px;
		height: 40px;
		border-radius: 20px;
		border-color: #fff;
		box-shadow:
		  4px 4px 6px #ccc,
		  -4px -4px 6px #fff;
		margin-top: 1em;
		font-family: 'Poppins', sans-serif;
		font-weight: 400;
		
	  }
	button:hover.form  {
	  cursor: pointer;
	}
	
	button:active.form  {
	  box-shadow:
		inset 4px 4px 6px #ccc,
		inset -4px -4px 6px #fff;
	}
	
	button:focus.form  {
	  outline: none;
	}
  
  
  ::placeholder .form {
	font-family: 'Poppins', sans-serif;
	font-weight: 500;
	color: #aaa;
  }
  
  a.form  {
	text-decoration: none;
	color: #777;
	font-size: 15px;
	transition: all 0.2s ease-in-out;
	caret-color: transparent;
  }
  label.form {
	caret-color: transparent;
  }
	a:hover.form  {
	  color: #aaa;
	  cursor: pointer;
	}
	
	a:active.form  {
	  color: #ccc;
	  cursor: pointer;
	}
	
	a:focus.form  {
	  outline: none;
	  cursor: pointer;
	}
	*:focus.form  {
		outline: none;
		cursor: pointer;
	}
	span.form {
		caret-color: transparent;
	}

	div.form {
		caret-color: transparent;
	}
	input.form {
		caret-color: transparent;
	}



