table {
    border-collapse: collapse;
    width: "100%";
    font-size: 10px;
  }
  
  th, td {
    text-align: left;
    padding: 8px;
  }
  

  
  th {
    color: #000000;
  }

  .cardTable {
    background: #f6f5f7;
    border-radius: 2px;
    display: inline-block;
    height: "100%";
    position: relative;
    width: 1000px;
  }
  
  .card-1Table {
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  }
  